<template>
    <div>
      <md-progress-bar class="md-accent" md-mode="indeterminate" v-if="loaded"></md-progress-bar>
        <md-table v-model="searched" md-sort="name" md-sort-order="asc" md-card md-fixed-header>
        <md-table-toolbar>
            <div class="md-toolbar-section-start">
            <h1 class="md-title">Blocks</h1>
            </div>

            <md-field md-clearable class="md-toolbar-section-end">
            <md-input placeholder="Search by id..." v-model="search" @input="searchOnTable" />
            </md-field>
        </md-table-toolbar>

        <md-table-empty-state
            md-label="No trailer id found"
            :md-description="`No trailer id found for this '${search}' query. Try a different search term.`">
        </md-table-empty-state>

        <md-table-row slot="md-table-row" slot-scope="{ item }">
            <md-table-cell md-label="ID" md-sort-by="id" md-numeric>{{ item.id }}</md-table-cell>
            <md-table-cell md-label="user" md-sort-by="first_name">{{ item.user }}</md-table-cell>
            <md-table-cell md-label="Actions"><md-menu md-size="medium" md-align-trigger>
                <md-button class="md-icon-button md-raised" md-menu-trigger @click="trailerid(item.id)">
                    <md-icon>lock_open</md-icon>
                </md-button>
                </md-menu>
            </md-table-cell>
        </md-table-row>
        </md-table>
    </div>
</template>

<script>
import axios from 'axios'

const toLower = text => {
  return text.toString().toLowerCase()
}
const searchByName = (items, term) => {
  if (term) {
    return items.filter(item => toLower(item.first_name).includes(toLower(term)))
  }
  return items
}
export default {
  name: 'Blocks',
  data: () => ({
    url: 'http://ben-logistics-api.eu-4.evennode.com/graphql',
    loaded: null,
    search: null,
    searched: []
  }),
  methods: {
    searchOnTable () {
      this.searched = searchByName(this.users, this.search)
    },
    trailerid (id) {
      axios.post(this.url, {
        query: `mutation { deleteTempTrailer(id:` + id + `){ id user } }`
      })
      this.refresh()
      // .then(response => {
      //   this.searched = response.data.data.temptrailer
      //   this.loaded = false
      // }).catch(e => { console.log(e) })
    },
    refresh () {
      this.loaded = true
      axios.post(this.url, {
        query: `{ temptrailer { id user } }`
      }).then(response => {
        this.searched = response.data.data.temptrailer
        this.loaded = false
      }).catch(e => { console.log(e) })
    }
  },
  created: function () {
    this.refresh()
  }
}
</script>

<style lang="scss" scoped>
  .md-card {
    width: 50%;
    margin: 4px;
    display: inline-block;
    vertical-align: top;
  }
</style>



