<template>
  <div>
    <md-progress-bar class="md-accent" md-mode="indeterminate" v-if="loaded"></md-progress-bar>
    <md-dialog :md-active.sync="showDialog">
      <md-card>
        <md-card-header>
          <div class="md-title">Add System User</div>
          <md-field>
              <label>First Name</label>
                <md-input v-model="newUser.first_name" required></md-input>
              <span class="md-error">There is an error</span>
          </md-field>
          <md-field>
              <label>Last Name</label>
                <md-input v-model="newUser.last_name" required></md-input>
              <span class="md-error">There is an error</span>
          </md-field>
          <md-field>
              <label>Login</label>
                <md-input v-model="newUser.login" required></md-input>
              <span class="md-error">There is an error</span>
          </md-field>
          <md-field>
              <label>Password</label>
                <md-input v-model="newUser.password" required></md-input>
              <span class="md-error">There is an error</span>
          </md-field>
          <md-field>
              <label>Post</label>
                <md-select v-model="newUser.post" name="post" id="post" required>
                  <md-option value="admin">Admin</md-option>
                  <md-option value="dispatcher">Dispatcher</md-option>
                </md-select>
              <span class="md-error">There is an error</span>
          </md-field>
          <md-checkbox v-model="newUser.active" value="true">Active</md-checkbox>
        </md-card-header>

        <md-card-actions>
          <md-button class="md-primary" @click="newUser = {}">Clear</md-button>
          <md-button class="md-primary" @click="performAdd()">Create</md-button>
        </md-card-actions>
      </md-card>
    </md-dialog>

    <md-table v-model="searched" md-sort="name" md-sort-order="asc" md-card md-fixed-header>
      <md-table-toolbar>
        <div class="md-toolbar-section-start">
          <h1 class="md-title">Users</h1>
        </div>
        <md-button class="md-icon-button" @click="showDialog = true">
          <md-icon>person_add</md-icon>
        </md-button>
        <md-field md-clearable class="md-toolbar-section-end">
          <md-input placeholder="Search by label..." v-model="search" @input="searchOnTable" />
        </md-field>
      </md-table-toolbar>

      <md-table-empty-state
        md-label="No users found"
        :md-description="`No users found for this '${search}' query. Try a different search term.`">
      </md-table-empty-state>

      <md-table-row slot="md-table-row" slot-scope="{ item }">
        <md-table-cell md-label="First name" md-sort-by="first_name">{{ item.first_name }}</md-table-cell>
        <md-table-cell md-label="Last name" md-sort-by="last_name">{{ item.last_name }}</md-table-cell>
        <md-table-cell md-label="Login" md-sort-by="login">{{ item.login }}</md-table-cell>
        <md-table-cell md-label="Post" md-sort-by="post">{{ item.post }}</md-table-cell>
      </md-table-row>
    </md-table>
    <md-snackbar :md-active.sync="showSnackbar">{{ msg }}</md-snackbar>
  </div>
</template>

<script>
import axios from 'axios'

const toLower = text => {
  return text.toString().toLowerCase()
}
const searchByName = (items, term) => {
  if (term) {
    return items.filter(item => toLower(item.first_name).includes(toLower(term)))
  }
  return items
}
export default {
  name: 'Dispatchers',
  data: () => ({
    url: 'http://ben-logistics-api.eu-4.evennode.com/graphql',
    // url: 'http://localhost:3000/graphql',
    loaded: true,
    showDialog: false,
    showSnackbar: false,
    msg: '',
    newUser: {},
    search: null,
    searched: [],
    users: null
  }),
  methods: {
    searchOnTable () {
      this.searched = searchByName(this.users, this.search)
    },
    performAdd () {
      if (this.newUser.first_name && this.newUser.last_name && this.newUser.login && this.newUser.password && this.newUser.post) {
        this.loaded = true
        var qr = ''
        if (this.newUser.first_name) qr += 'first_name:"' + this.newUser.first_name + '",'
        if (this.newUser.last_name) qr += 'last_name:"' + this.newUser.last_name + '",'
        if (this.newUser.login) qr += 'login:"' + this.newUser.login + '",'
        if (this.newUser.password) qr += 'password:"' + this.newUser.password + '",'
        if (this.newUser.post) qr += 'post:"' + this.newUser.post + '",'
        if (this.newUser.active === 'true') qr += 'active:"' + this.newUser.active + '",'
        else qr += 'active:"false",'
        qr = qr.substring(0, qr.length - 1)
        axios.post(this.url, {
          query: `mutation{ addUser(` + qr + `) { login } }`
        }).then(res => {
          this.loaded = false
          this.loadData()
        }).catch(e => {
          this.msg = 'User create error!'
          this.showSnackbar = true
          this.loaded = false
        })
      } else {
        this.msg = 'Fields cannot be empty!'
        this.showSnackbar = true
      }
    },
    loadData () {
      axios.post(this.url, {
        query: `{ users{ first_name last_name login post active} }`
      }).then(res => {
        this.users = res.data.data.users
        this.searched = this.users
        this.loaded = false
      })
    }
  },
  created () {
    this.loadData()
  }
}
</script>



